/* General body styling */
body {
    background: linear-gradient(to right, #74ebd5, #acb6e5);
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    margin: 0;
  }
  
  /* Card styling */
  .card {
    max-width: 400px;
    margin: auto;
    border: none;
    border-radius: 10px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.15);
    background-color: #ffffff;
  }
  
  /* Card title */
  .card-body h2 {
    font-weight: 600;
    color: #333;
  }
  
  /* Alert message */
  .alert {
    font-size: 0.9rem;
  }
  
  /* Form styling */
  form .form-label {
    font-weight: 500;
    color: #555;
  }
  
  form .form-control {
    border-radius: 6px;
    border: 1px solid #ccc;
    padding: 10px;
    transition: border-color 0.3s ease;
  }
  
  form .form-control:focus {
    border-color: #5a9bf6;
    box-shadow: 0 0 0 0.2rem rgba(90, 155, 246, 0.25);
  }
  
  /* Button styling */
  button[type="submit"] {
    background-color: #5a9bf6;
    border: none;
    font-weight: 500;
    padding: 10px 0;
    border-radius: 6px;
    transition: background-color 0.3s ease;
  }
  
  button[type="submit"]:hover {
    background-color: #4a8be3;
  }
  
  button[disabled] {
    background-color: #aac8f3;
  }
  
  /* Bottom link */
  .w-100.text-center.mt-2 {
    color: #333;
    font-size: 0.9rem;
  }
  
  .w-100.text-center.mt-2 a {
    color: #5a9bf6;
    text-decoration: none;
  }
  
  .w-100.text-center.mt-2 a:hover {
    text-decoration: underline;
  }
  